$prefix: '.wc-booster';

.wc-booster-category-collage-cat-title a{
	text-decoration: none !important;
}

.wc-booster-category-collage-body-inner {
	position: relative;
}

.wc-booster-category-collage-inner-wrapper {
    background-position: center;
    background-size: cover;
    margin: 10px;
    display: flex;
    flex-direction: column-reverse;
    position: relative;
}

.wc-booster-category-collage-wrapper {
    display: flex;
    flex-wrap: wrap;
	margin: 0 -10px;
	&.wc-booster-category-collage-layout-one {
		.wc-booster-category-collage-card-wrapper {
			flex: 50%;
	
			&:first-child{
				flex: 60%;
			}
			&:nth-child(2){
				flex: 40%;
			}
	
			&:nth-child(3){
				flex: 40%;
			}
			&:nth-child(4),
			&:nth-child(5){
				flex: 30%;
			}
	
			@media (max-width: 620px) {
				flex: 100%;
			}
		}
	}
	&.wc-booster-category-collage-layout-two {
		.wc-booster-category-collage-layout-two-wrapper {
			max-width: 70%;
			flex: 0 0 70%;
			width: 100%;
			display: flex;
			flex-wrap: wrap;
			.wc-booster-category-collage-card-wrapper {
				max-width: 50%;
				flex: 0 0 50%;
				width: 100%;

				&:first-child {
					max-width: 40%;
					flex: 0 0 40%;
				}

				&:nth-child(2) {
					max-width: 60%;
					flex: 0 0 60%;
				}

				&:first-child {
					max-width: 40%;
					flex: 0 0 40%;
				}

				&:nth-child(3) {
					max-width: 55%;
					flex: 0 0 55%;
				}

				&:nth-child(4) {
					max-width: 45%;
					flex: 0 0 45%;
				}
			}

			@media (max-width: 620px) {
				max-width: 100%;
				flex: 0 0 100%;

				.wc-booster-category-collage-card-wrapper {
					max-width: 100%;
					flex: 0 0 100%;
				}
			}
		}

		.wc-booster-category-collage-card-wrapper.category-collage-right-card {
			flex: 0 0 30%;
			width: 100%;
			 > div {
				height: calc(100% - 20px) !important;
			}

			
			@media (max-width: 620px) {
				max-width: 100%;
				flex: 0 0 100%;
				padding-left: 0;
			}
		}
	}	

}

.wc-booster-category-collage-overlay {
	position: absolute;
	left: 0;
    height: 100%;
    width: 100%;
    top: 0;
}